h1 {
  text-transform: uppercase;
  font-size: 42px;
  color: white;
  margin: 30px 30px; }

h2, h3, h4 {
  text-transform: capitalize;
  color: #46344e;
  text-shadow: none; }

body {
  font-family: "Roboto", sans-serif;
  background-color: #b4dfe5;
  color: #46344e; }

header {
  padding: 0px;
  display: flex;
  height: 10%;
  width: 100%;
  background-color: #46344e; }

main {
  margin: 30px 30px; }

.series__container h2 {
  width: 100%; }

.series__container-list {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
  width: 100%;
  margin-top: 15px;
  background-color: white; }
  .series__container-list:last-child {
    align-items: flex-start; }

.results-item {
  border: solid 1px #eeeeee;
  border-radius: 5px;
  margin: 15px 10px;
  position: relative;
  width: 30%;
  max-width: 250px; }
  .results-item-favourite {
    text-shadow: -0.5px -0.5px 2px white;
    position: absolute;
    top: 12px;
    right: 12px;
    color: grey;
    cursor: pointer;
    transition: all .3s ease-in-out; }
    .results-item-favourite:hover {
      text-shadow: 0px 0px 2px #46344e;
      color: #fce181; }
  .results-item.favourite .results-item-favourite {
    text-shadow: 0px 0px 2px #46344e;
    color: #fce181; }
  .results-item img {
    display: block;
    width: 100%; }

.img-fav-size div {
  height: auto;
  width: auto;
  max-width: 150px;
  max-height: 1500px; }
